.heading{
    font-size: xx-large;
    font-style: bolder;
    padding-left: 169px;
}

.calculatori{
    display: flex;
    flex-direction: column;
    padding-top: 50px;
    padding-right: 3px;
    width: 100%;
    max-width: 500px;
    height: 600px;
    background-color:pink;
    border-radius: 7%;
    overflow: hidden;
    box-shadow: 0px 2px 64px rgba(0, 0, 0, 0.2);
    align-items: center;
    border: 10px solid black;
    box-shadow: 20px 20px grey;
    text-align: center;
}

.displayi{
    padding: 16px;
    text-align: right;
    background-color: rgb(189, 185, 185);
    color: var(--dark);
    font-size: xx-large;
    font-weight: bolder;
    border-radius: 10px;
    margin-left: 3px;
    box-shadow: 3px 3px grey;
    margin-bottom: 5px;
    height: 80px; 
    width: 90%;
}

.appi{
    margin-left: 33%;
}
button {
        -webkit-appearance: none;
        appearance: none;
        border: none;
        border-radius: 10px;
        color: #000;
        cursor: pointer;
        font-size: 20px;
        outline: none;
        padding: 16px;
        margin-top: 7px;
      }